/*--------------------------------------------------------------
 *# General
 *-------------------------------------------------------------- */
*,
*:before,
*:after {
  padding: 0;
  margin: 0;
  border: 0;
  box-sizing: border-box; }

body {
  height: 100%;
  font-family: "Open Sans", sans-serif;
  color: #ffffff;
  background-color: #353535; }

.main-background {
  background: url("../img/hero.png") top center no-repeat;
  background-size: cover; }

.wrap {
  max-width: 1400px;
  margin: 0 auto; }

a {
  color: #bb0f0f;
  text-decoration: none;

  &:hover {
    color: #bb0f0f;
    text-decoration: none; } }

h1, h2, h3, h4, h5, h6 {
  font-family: "Open Sans", sans-serif; }

/*--------------------------------------------------------------
 *# Sections General
 *-------------------------------------------------------------- */
section {
  padding: 60px 0;
  overflow: hidden;
  position: relative; }

.section-dark {
  padding: 120px 0;
  color: #fff;

  &:before {
    content: "";
    position: absolute;
    background: #242424;
    bottom: 50px;
    top: 50px;
    left: 0;
    right: 0; } }

.section-title {
  text-align: center;
  padding: 0 7% 30px 7%;
  position: relative;
  @media ( min-width: 992px ) {
    padding: 0 20% 30px 20%; }

  h2 {
    font-size: 32px;
    text-shadow: 2px 2px 5px #000000;
    font-weight: bold;
    text-transform: uppercase;
    margin-bottom: 20px;
    padding-bottom: 20px;
    position: relative;

    &::after {
      content: '';
      position: absolute;
      display: block;
      width: 50px;
      height: 3px;
      background: #bb0f0f;
      bottom: 0;
      left: calc(50% - 25px); } }

  p {
    margin-bottom: 0; } }

/*--------------------------------------------------------------
 *# Back to top button
 *-------------------------------------------------------------- */

.back-to-top {
  position: fixed;
  visibility: hidden;
  opacity: 0;
  right: 0;
  bottom: 15px;
  z-index: 996;
  background: #000000a9;
  width: 40px;
  height: 40px;
  border-radius: 4px;
  transition: all 0.4s;
  @media (max-width: 767.98px) {
    left: 0; }
  i {
    font-size: 28px;
    color: #fff;
    line-height: 0;
    transition: 0.4s; }
  @media (min-width: 767.98px) {
    &:hover {
      width: 55px;
      i {
        color: #bb0f0f;
        transition: .4s; } } }

  &.active {
    visibility: visible;
    opacity: 1; } }

.text-call {
  position: fixed;
  right: 0;
  bottom: 10px;
  z-index: 996;
  background: #000000a9;
  width: 40px;
  height: 40px;
  border-radius: 4px;
  transition: all 0.4s;
  @media (max-width: 767.98px) {
    left: 0; }
  i {
    color: #fff;
    font-size: 20px;
    line-height: 0;
    transition: 0.4s; }
  @media (min-width: 767.98px) {
    &:hover {
      width: 55px;
      i {
        color: #bb0f0f;
        transition: .4s; } } } }

.back-call {
  margin-bottom: 225px; }

.youtube {
  margin-bottom: 170px; }

.instagramm {
  margin-bottom: 115px; }

.facebook {
  margin-bottom: 60px; }

/*--------------------------------------------------------------
 *# Disable aos animation delay on mobile devices
 *-------------------------------------------------------------- */
@media screen and (max-width: 768px) {
  [data-aos-delay] {
    transition-delay: 0 !important; } }

/*--------------------------------------------------------------
 *# Header
 *-------------------------------------------------------------- */

#header {
  background-color: #353535;
  transition: all 0.5s;
  z-index: 997;
  padding: 0;
  box-shadow: 0px 0px 8px #000;
  &.header-scrolled {
    padding: 0;
    box-shadow: 0px 8px 8px #000; }

  .logo {
    img {
      max-height: 60px;
      padding: 0; } }
  .round {
    width: 40px;
    color: red;
    .language {
      color: #fff600;
      text-decoration: underline;
      border-radius: 5px; } } }


/*--------------------------------------------------------------
 *# Navigation Menu
 *-------------------------------------------------------------- */
/**
 ** Desktop Navigation */

.navbar {
  padding: 0;
  background-color: #353535;

  ul {
    margin: 0;
    padding: 0;
    display: flex;
    list-style: none;
    align-items: center; }

  li {
    position: relative; }

  a {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 0 10px 30px;
    font-family: "Raleway", sans-serif;
    font-size: 15px;
    font-weight: 600;
    color: #ffffff;
    white-space: nowrap;
    transition: 0.4s;

    i {
      font-size: 12px;
      line-height: 0;
      margin-left: 5px; }

    &:hover {
      color: #bb0f0f; } }

  .active, li:hover > a {
    color: #bb0f0f; }


  .dropdown {
    ul {
      display: block;
      position: absolute;
      left: -100px;
      top: calc(100% + 30px);
      margin: 0;
      padding: 10px 0;
      z-index: 99;
      opacity: 0;
      visibility: hidden;
      border: 2px solid #2b2a2a;
      border-radius: 3px;
      background: #bb0f0f;
      box-shadow: 10px 10px 7px #000000;
      transition: 0.4s;

      li {
        min-width: 200px; }

      a {
        padding: 10px 20px;
        font-size: 16px;

        i {
          font-size: 12px; }

        &:hover {
          color: #000; } }

      .active:hover, li:hover > a {
        color: #000; } }

    &:hover > ul {
      opacity: 1;
      top: 100%;
      visibility: visible; }

    .dropstart {
      ul {
        top: -100px;
        left: calc(100% - 30px);
        visibility: hidden; }

      &:hover > ul {
        opacity: 1;
        top: 0;
        left: 100%;
        visibility: visible; } } } }

@media (max-width: 1366px) {
  .navbar .dropdown .dropdown {
    ul {
      left: -90%; }

    &:hover > ul {
      left: -100%; } } }

/*--------------------------------------------------------------
 *# Mobile Navigation
 *-------------------------------------------------------------- */

.mobile-nav-toggle {
  color: #bb0f0f;
  font-size: 50px;
  cursor: pointer;
  display: none;
  line-height: 0;
  transition: 0.5s;
  // margin-left: 85%

  &.bi-x {
    color: #bb0f0f;
    margin-left: 82%; } }

@media (max-width: 991px) {
  .mobile-nav-toggle {
    display: block; }

  .navbar ul {
    display: none; } }

.navbar-mobile {
  position: fixed;
  overflow: hidden;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.9);
  transition: 0.3s;
  z-index: 999;

  .mobile-nav-toggle {
    position: absolute;
    top: 15px;
    left: 15px; }

  ul {
    display: block;
    position: absolute;
    top: 55px;
    right: 15px;
    bottom: 15px;
    left: 15px;
    padding: 10px 0;
    background-color: #353535;
    overflow-y: auto;
    transition: 0.3s; }

  a {
    padding: 10px 20px;
    font-size: 16px;
    color: #ffffff;
    @media (min-width: 768px) {
      font-size: 20px; } }

  .getstarted {
    margin: 15px; }

  .dropdown {
    ul {
      position: static;
      display: none;
      margin: 10px 20px;
      padding: 10px 0;
      z-index: 99;
      opacity: 1;
      visibility: visible;
      background: #bb0f0f;
      box-shadow: #000000 0 0 10px 5px;

      li {
        min-width: 200px; }

      a {
        padding: 10px 20px;
        font-size: 10px;
        @media (min-width: 370px) {
          font-size: 13px; }
        @media (min-width: 768px) {
          font-size: 16px; }

        i {
          font-size: 12px; }

        &:hover {
          color: #bb0f0f; } }

      // .active:hover, li:hover > a
 }      //   color: #bb0f0f

    > .dropdown-active {
      display: block; } } }

