//Пульсирующая кнопка акции
.pulse-button {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  width: 80px;
  height: 80px;
  border-radius: 40px;
  padding: 10px;
  border: none;
  background: #bb0f0f;
  box-shadow: 0 5px 35px rgba(0, 0, 0, 0.2);
  position: fixed;
  bottom: 30px;
  right: 10px;
  cursor: pointer;
  outline: none;
  z-index: 1000;
  @media (min-width: 992px) {
    left: 10px; } }

.pulse-button__text {
  display: block;
  width: 100%;
  height: 43px;
  font: 12px 'Open Sans', sans-serif;
  color: #fff;
  text-shadow: 1px 1px 1px #000000;
  text-align: center;
  line-height: 1.2;
  opacity: 1;
  position: absolute;
  transition: .3s;
  margin-top: 1px; }

.pulse-button__rings {
  border: 5px solid #bb0f0f;
  box-shadow: 2px 2px 10px #bb0f0f;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  height: auto;
  width: auto;
  border-radius: 50%;
  animation-duration: 2.0s;
  animation-name: pulse_1;
  animation-iteration-count: infinite;
  z-index: -1; }

.pulse-button__rings:nth-child(2) {
  animation-name: pulse_2; }

.pulse-button__rings:nth-child(3) {
  animation-name: pulse_3; }

@keyframes pulse_1 {
  from {
    opacity: 1;
    transform: scale(0.9, 0.9); }


  to {
    opacity: 0;
    transform: scale(1.6, 1.6); } }

@keyframes pulse_2 {
  from {
    opacity: 1;
    transform: scale(2, 2); }


  to {
    opacity: 0;
    transform: scale(1.8, 1.8); } }

@keyframes pulse_3 {
  from {
    opacity: 1;
    transform: scale(0.8, 0.8); }


  to {
    opacity: 0;
    transform: scale(2.3, 2.3); } }







